.navbar{
    background-color: rgb(55, 66, 90);
    height:80px;
    display: flex;
    justify-content: center;
    font-size: 1.2rem;
    position: sticky;
    top:0;
    z-index: 20;
}

.nav-log{
    color: aliceblue;
    align-items: center;
    margin-left:20px;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
    flex-grow:1;
}
.nav-menu{
    display: flex;
    list-style: none;
    text-align: center;
    margin-right: 2rem;
}
.nav-link{
    color: antiquewhite;
    text-decoration: none;
    padding: 0.5rem 1rem;
    height:100%;
    border-bottom: 3px solid transparent;
}

.nav-item{
    line-height: 40px;
    margin-right:1rem;
}
.nav-item:after{
    content: "";
    display: block;
    height: 3px;
    width: 0;
    background: transparent;
    transition: width 0.7s ease, backgroung-color 0.5s ease-in;
}
nav.item:hover:after{
    width:100%;
    background: yellow;
} 
.nav-item .active{
    color:yellow;
    border: 1px solid yellow;
}
.nav-icon{
    display: none;
}
 @media screen and (max-width: 960px){
    .nav-menu{
      display: flex;
      flex-direction: column;
      width: 100%;
      border-top:1px solid white;
      position:absolute;
      top:80px;
      left:-110%;
      opacity: 1;
    }
    .nav-menu.active{
        background:rgb(55, 66, 90);
        left:0px;
        opacity: 1;
        transition: all 0.5s ease;
        z-index: 1;
      }
  
      .nav-icon{
          display: block;
          margin-right: 10px;
          
      }
  }